import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useEffect } from 'react';

import { LinkBlank, LoadingIndicator } from '@mssgme/ui';
import { useUploads, MediaSizes } from '../../../hooks';

import styles from './ImageGallery.scss';

export const ImageSlide = ({ index, image, style, theme: { ratio }, onClick, swiper }) => {
    const { getMediaSizeUrl } = useUploads();
    const absoluteUrl = getMediaSizeUrl(image.url, MediaSizes.Large);
    const shouldFit = ratio !== 'original';
    const hasLink = !!image.externalUrl;

    const Tag = hasLink ? LinkBlank : 'div';

    const handleLoad = useCallback(() => void (swiper && swiper.updateAutoHeight()), [swiper]);
    const handleClick = useCallback(() => void (onClick && (!hasLink && image.modal) && onClick(index)), [
        index,
        onClick,
        hasLink,
        image.modal,
    ]);
    const attributes = {
        className: styles.imageContainer,
        onClick: handleClick,
        ...(hasLink && { href: image.externalUrl }),
    };

    useEffect(() => {
        if (swiper?.lazy?.initialImageLoaded) {
            swiper.lazy.loadInSlide(index);
        }
    }, [index, image]);

    return (
        <div
            className={cn(styles.imageBlock, {
                [styles.placeholder]: !absoluteUrl,
                [styles.fitOutside]: shouldFit,
                [styles[ratio]]: styles[ratio],
            })}
            style={style}
        >
            <Tag {...attributes}>
                <div className={styles.image}>
                    <img
                        alt={image.annotation || 'Image'}
                        className="swiper-lazy"
                        data-src={absoluteUrl}
                        onLoad={handleLoad}
                    />
                </div>
                <div className={styles.preloader}>
                    <LoadingIndicator />
                </div>
            </Tag>

            {image.annotation && <div className={styles.annotation}>{image.annotation}</div>}
        </div>
    );
};

ImageSlide.propTypes = {
    index: PropTypes.number.isRequired,
    image: PropTypes.shape({
        url: PropTypes.string.isRequired,
        externalUrl: PropTypes.string,
        modal: PropTypes.bool,
        annotation: PropTypes.string,
    }).isRequired,
    theme: PropTypes.object.isRequired,
    style: PropTypes.object,
    onClick: PropTypes.func,
    swiper: PropTypes.object,
};
